<template><section><html><head></head><body><h1>Polyline</h1>
<p>The <code v-pre="">vc-primitive-polyline</code> component is used to load a renderable polyline, that is created and rendered using a <code v-pre="">vc-collection-primitive-polyline</code>.</p>
<h2>Example</h2>
<h3>Load polylines with <code v-pre="">vc-primitive-polyline</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" scene3-d-only="">
        <vc-collection-primitive-polyline>
          <vc-primitive-polyline :positions="positions1" :material="material1" :width="5"></vc-primitive-polyline>
          <vc-primitive-polyline :positions="positions2" :material="material2" :width="10"></vc-primitive-polyline>
          <vc-primitive-polyline :positions="positions3" :material="material3" :width="10"></vc-primitive-polyline>
        </vc-collection-primitive-polyline>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
