<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre="">optional</code> true if this polyline will be shown; otherwise, false.</td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td>1.0</td>
<td><code v-pre="">optional</code> The width of the polyline in pixels.</td>
</tr>
<tr>
<td>loop</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> Whether a line segment will be added between the last and first line positions to make this line a loop.</td>
</tr>
<tr>
<td>material</td>
<td>Object</td>
<td>Material.ColorType</td>
<td><code v-pre="">optional</code> The material.</td>
</tr>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre="">optional</code> The positions.</td>
</tr>
<tr>
<td>id</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The user-defined object to be returned when this polyline is picked.</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The condition specifying at what distance from the camera that this polyline will be displayed.</td>
</tr>
</tbody>
</table>
